import { useEffect, useState } from 'react'
import axios from 'axios'
import Card from './Card'

function UserCard() {
const [feedIDUsers, setFeedIDUsers] = useState([])
const feedIDArr = ['az11136990256', 'az11150422709', 'az11150157984', 'az11044601983', 'az10995808516', 'az10917500238']

const feedIDURLArr = feedIDArr.map((item, index) => {
  return '/api/inc/microvideo/share/profiles?feedids='+item
})

console.log(feedIDURLArr)

  useEffect(() => {

    const getAllUser =  () => {
      Promise.all(feedIDURLArr.map((itemURL) => axios.get(itemURL)))
        .then(
          axios.spread((...data) => {
            console.log(data)

            localStorage.setItem('getFeedUser', JSON.stringify(data)) 
            setFeedIDUsers(data)
          })
        )
    }
    if (!localStorage.getItem('getFeedUser')) {
      console.log('0000000')
      getAllUser()
    } else {
      console.log(1111111)
      const data = JSON.parse(localStorage.getItem('getFeedUser'))
      setFeedIDUsers(data)
    }
  }, [feedIDURLArr])
  console.log(feedIDUsers)
  return (
    <div className="usercard">
    {
      feedIDUsers.length > 0 && feedIDUsers.map((item, index) => <Card key={index} muser={item} /> )
    }
    </div>
  )
}


export default UserCard

